.__select {
  position: relative;
  width: 230px;
  height: 40px;
  margin: 0 auto;
  left: -5%;

  .__select:hover {
    border: 1px solid #4e7cca;
  }

  &[data-state="active"] {
    .__select__title {
      &::before {
        transform: translate(-3px, -50%) rotate(-45deg);
      }

      &::after {
        transform: translate(3px, -50%) rotate(45deg);
      }
    }

    .__select__content {
      opacity: 1;
    }

    .__select__label + .__select__input + .__select__label {
      max-height: 40px;
      border: none;
    }
  }
}
.__select__title {
  display: flex;
  align-items: center;
  width: 393px;
  height: 100%;
  padding: 8px 16px;
  font-family: Montserrat;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 16px;
  border-radius: 5px;
  border: solid 1px black;
  cursor: pointer;

  &::before,
  &::after {
    content: "";

    position: absolute;
    top: 50%;
    right: -146px;

    display: block;
    width: 10px;
    height: 2px;

    transition: all 0.3s ease-out;

    background-color: #333333;

    transform: translate(-3px, -50%) rotate(45deg);
  }
  &::after {
    transform: translate(3px, -50%) rotate(-45deg);
  }

  &:hover {
    border-color: #4e7cca;

    &::before,
    &::after {
      background-color: black;
    }
  }
}
.reset {
  display: none;
  width: 230px;
  padding: 8px 16px;
  margin: 0 auto;
  margin-bottom: 10px;

  border: solid 1px #c7ccd1;
  border-radius: 5px;

  transition: all 0.2s ease-out;

  cursor: pointer;

  font-weight: bold;

  background-color: #ffffff;
  color: #333333;

  &:hover {
    background-color: #4e7cca;
    color: #ffffff;
  }
}
.__select__content {
  position: absolute;
  top: 46px;
  display: flex;
  flex-direction: column;
  width: 393px;
  background-color: #ffffff;
  border: none;
  border-top: none;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  box-shadow: 0 0 26px 0 rgb(0 0 0 / 50%);
  max-height: 214px;
  transition: all 0.3s ease-out;
  opacity: 0;
  z-index: 8;
  overflow: scroll;
}
.__select__input {
  display: none;

  &:checked + label {
    background-color: #dedede;
  }
  &:disabled + label {
    opacity: 0.6;
    pointer-events: none;
  }
}
.__select__label {
  display: flex;
  align-items: center;
  width: 100%;
  min-height: 40px;
  padding: 0 16px;
  font-family: Montserrat;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 16px;

  transition: all 0.2s ease-out;

  cursor: pointer;

  overflow: hidden;

  & + input + & {
    border-top: 0 solid #c7ccd160;
  }

  &:hover {
    background-color: #4e7cca !important;

    color: #ffffff;
  }
}

